import * as React from 'react';
import { css } from 'office-ui-fabric-react';
import { IPropertyPanePageProps } from './IPropertyPanePage';
import PropertyPaneGroup from '../propertyPaneGroup/PropertyPaneGroup';
import propertyPanePageStyles from './PropertyPanePage.module.scss';

export default class PropertyPanePage extends React.Component<IPropertyPanePageProps, void> {

  private _propertyPanePage: HTMLDivElement;

  public componentDidMount(): void {
     this.props.setContainerDiv(this._propertyPanePage);
  }

  public render(): React.ReactElement<{}> {
    const propertyGroups: React.ReactElement<{}>[] = this.props.propertyGroups.map(
      group => (
        <PropertyPaneGroup
          key={ group.groupName }
          groupName={ group.groupName }
          groupFields={ group.groupFields }
          onChange={ this.props.onChange }
          properties={ this.props.properties }
          setEntryState={ this.props.setEntryState }
          displayGroupsAsAccordion={ this.props.displayGroupsAsAccordion }
          />
      ));

    return (
      <div>
        <div className={ propertyPanePageStyles.propertyPanePageContent }
          ref= { (c: HTMLDivElement): HTMLDivElement => this._propertyPanePage = c }>
          { this.props.pageDescription
            ? <div
              className={ css(propertyPanePageStyles.propertyPanePageDescription, 'ms-font-m-plus') }
                id='propertyPaneDescriptionId'>
                  { this.props.pageDescription }
              </div>
            /* tslint:disable:no-null-keyword */
            : null
            /* tslint:enable:no-null-keyword */
          }
          <div>
            { propertyGroups }
          </div>
        </div>
      </div>
    );

  }
}
